body {
    background-color: #bbb;

}

@keyframes fade-in {
    0% {
      opacity: 0;
      transform: scale(0.95);
    }
    70% {
        transform: scale(1.01);
    }
    100% {
        opacity: 1;
        transform: scale(1);
    }
  }

.page {
    width: 21.0cm;
    height: 29.7cm;
    background-color: #fff;
    box-shadow: 5px 5px 5px #555;
    margin: 50px auto;

    animation: 500ms ease-out 0s 1 fade-in;
}
